// src/pages/Home.js
import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Waitlist from "./Waitlist";
import "./Home.css";

const Home = ({ currentUser }) => {
  const [showWaitlist, setShowWaitlist] = useState(false);
  const navigate = useNavigate();

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowWaitlist(true);
    }, 1000);
    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="home-container">
      <h1 className="home-title">👋 Welcome to WhiffGone</h1>
      <p className="home-subtitle">
        Fresh Confidence, Instantly. For those who live fast and smell fresh.
      </p>
      <button className="shop-button" onClick={() => navigate("/products")}>Shop Now</button>
      <p className="join-club">Join the Club &rarr;</p>
      {showWaitlist && <Waitlist onClose={() => setShowWaitlist(false)} />}
    </div>
  );
};

export default Home;














